import React, { Component } from 'react';

import TableData from 'table-data';

export default class App extends Component {
  render () {
    const tableItemsList = [{id: 1, name: 'User 1'}, {id: 2, name: 'User 2'}, {id: 3, name: 'User 3'}];
    const keyList = ['id', 'name'];
    const tableColumnData = {id: 'ID', name: 'Full Name'};
    return (
      <div>
        <TableData tableItemsList={tableItemsList} keyList={keyList} tableColumnData={tableColumnData} tableRowClick={() => {}} numberOfRows={5} />
      </div>
    );
  }
}



